  height: 0.125rem;
  transition: 0.5s;
  transition: 0.3s ease;
  border-radius: 10px;
  background-color: #fff;
}

.header__hamburger span:nth-child(1) {
  top: -8px;
}

.header__hamburger span:nth-child(2) {
