  color: #333333;
  background: #fff;
}

.pc-nav__item--white:hover {
  color: #fff;
  background-color: #333333;
}

li:not(:last-child).pc-nav__item a::after {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: calc(100% - 1rem);
  height: 1px;
  background: #ffea2e;
  bottom: 0.375rem;
  visibility: hidden;
}

li:not(:last-child).pc-nav__item a:hover::after {
  visibility: visible;
}

